@mixin fontSize($fontSize){
	font-size: $fontSize * 1px;
	font-size: ($fontSize / 10) * 1rem;
}

@import 'normalize';
@import 'typography';
@import 'navigation';
@import 'sidebar';
@import 'comments';
@import 'footer';
/*
====================================
Default Styling
====================================
*/

$orange: #EF3D37;
$bluegrey: #789ABB;
$grey: #4C5760;
$lightgrey: #93A8AC;
$blue: #434DB0;
$darkblue: #011627;
$black: #011627;

img {
	max-width: 100%;
}

body {
	font-family: 'raleway', sans-serif;
	font-weight: 400;
	color: #333;
	@include fontSize(16);
	-webkit-font-smoothing: antialiased;
}

.container {
	max-width: 1250px;
	margin: 0 auto;
}

.container2 {
	max-width: 80%;
	margin: 0 auto;
}

.content {
	float: left;
	width: 65%;
	margin-right: 5%;
}

.full {
	width: 100%;
}


// HEADER


.hero {
	background-image: url(images/tiff-headshot.jpg);
	background-size: cover;
	height: 100vh;
}

.container {
	width: 70%;
	margin: 0 auto;
	position: relative;
	h1 {
		margin: 37% 0 0 60%;
		color: white;
		text-transform: uppercase;
		font-weight: 300;
		@include fontSize(34);
		letter-spacing: 2px;
		p {
			font-weight: 800;
			padding: 0;
			margin: 0;
			position: relative;
			top: -20px;
			right: -5px;
			letter-spacing: 2px;
		}
	}	
	p.title {
		color: white;
		font-weight: 200;
		@include fontSize(10);
		position: absolute;
		bottom: -25px;
		right: 196px;
		&:after {
			content:" ";
			background-color: white;
			height: 1px;
			width: 50px;
			display: block;
			margin: 10px 0 0;
		}
	}
}


// ABOUT ME

.about {
	margin: 0 0 150px 0;
	span {
		font-weight: 300;
	}
}


h3 {
	color: $black;
	font-weight: 800;
	text-transform: uppercase;
	@include fontSize(26);
	text-align: center;
	padding: 50px 0;
	letter-spacing: 1px;
	&:after {
		content:" ";
		background-color: $black;
		height: 1px;
		width: 50px;
		display: block;
		margin: 20px auto 0;
	}
}

.container1 {
	max-width: 50%;
	margin: 0 auto;
}


.skill {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-around;
	.tech {
		margin-bottom: 30px;
	}
	i {
		// align-self: auto;
		text-align: center;
		margin: 8px 30px;
		padding: 10px;			
		@include fontSize(30);
		color: $orange;
		// border-radius: 50%;
	}
	p {
		@include fontSize(8);
		text-align: center;
		letter-spacing: 1.5px;
		font-weight: 800;
	}
	.skill {
		flex: 1 0 auto;
	}
}

.aboutMe {
	margin: 20px 0 20px;
	.short {
		text-align: right;
		font-weight: 800;
		float: left;
		width: 28.5%;
		margin: 0 3% 0 0;
		@include fontSize(12);
	}
	.long {
		float: left;
		width: 68.5%;
		text-align: left;
		@include fontSize(9);
		color: $grey;
	}
}

.resumeButton {
	background-color: $orange;
	width: 200px;
	text-align: center;
	margin: 0 0 0 32%;
	p {
		@include fontSize(9);
		padding: 7px;
	}
}		


// PORTFOLIO SECTION

section.portfolio {
	background-color: #898989;
	padding-top: 10px;
	h3 {
		padding: 60px 0;
	}
	.projects {
		color: white;
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		.projectPhoto {
			margin-top: 30px;
			flex: 1 0 45%;
			&nth-child(even) {
				order: 2;
			}
		}
		.projectInfo {
			margin-top: 30px;
			color: black;
			padding-top: 30px;
			flex: 1 0 50%;
			&nth-child(even) {
				order: -1;
			}
			p {
				@include fontSize(9);
			}
			.techUsed {
				@include fontSize(8);
				font-weight: 800;
				float: left;
			}
			h4 {
				clear: both;
				margin: 0;
			}
		}
	}
}	
section.portfolio .projects:nth-child(odd) {
	flex-direction: row-reverse;
}

// FOOTER


footer {
	h3 {
		padding: 60px 0 0 0;
		margin: 0;
		&:after {
			display: none;
		}
		span {
			font-weight: 200;
		}
	}
	a {
		&:hover {
			color: $orange;
		}
	}
	h4 {
		padding: 25px 0 0 0;
	}
	p.connect {
		@include fontSize(10);
		text-align: center;
		margin-left: -10%;
		position: relative;
		&:after {
			position: absolute;
			bottom: -12px;
			right: 50.8%;
			content:" ";
			background-color: black;
			height: 2px;
			width: 50px;
			display: block;
			margin: 10px 0 0;
			text-align: center;
		}
	}
}

.contactInformation {
	padding: 5% 0 0 0;
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	& > * {
		flex: 1 0 45%;
		margin: 2.5%;
	}
}

.contactInfo {
	text-align: right;
}

.social {
	padding: 5% 0 5%;
	text-align: center;
	a {
		padding: 0 10px;
	}
	p {
		@include fontSize(7);
		padding-top: 20px;
	}
}

// FORM

form {
	margin: 0 0 0 0;
	input {
		border: none;
		border-bottom: 1px solid black;
		padding: 0 0 25px 0;
		margin: 12px;
	}
	input[type=text] {
		width: 65%;
	}
	input[type=email] {
		width: 65%;
	}
	input[type=textarea] {
		padding-bottom: 55px;
		width: 65%;
	}
	input[type=submit] {
		border: none;
		@include fontSize(8);
		padding: 10px 60px;
		margin-top: 50px;
		text-transform: uppercase;
		color: black;
		background-color: $orange;
	}
}

::-webkit-input-placeholder {
	font-weight: 800;
	@include fontSize(11);
	color: black;
}

:-moz-placeholder { /* Firefox 18- */
font-weight: 800;
@include fontSize(11);
color: black;
}

::-moz-placeholder {  /* Firefox 19+ */
	font-weight: 800;
	@include fontSize(11);
	color: black;
}

:-ms-input-placeholder {  
    font-weight: 800;
    @include fontSize(11);
    color: black;
}

